import Vue from 'vue'
import Vuex from 'vuex'
import InitState from './states'
import mutations from './mutations'

Vue.use(Vuex)

const state = sessionStorage.getItem('store')
  ? JSON.parse(sessionStorage.getItem('store'))
  : InitState

export default new Vuex.Store({
  state,
  mutations,
  actions: {
  }
})
