<template>
  <div class="list">
    <header>
      <img src="../assets/index/sxt_ico.png" alt class="car">
      <Form inline>
        <FormItem class="slider-contain">
          <Slider v-model="form.vol" show-tip="never"></Slider>
          <Icon type="md-mic" class="icon" ></Icon>
        </FormItem>
        <FormItem class="slider-contain">
          <Slider v-model="form.son" show-tip="never"></Slider>
          <Icon type="md-volume-up" class="icon" ></Icon>
        </FormItem>
      </Form>
      <img src="../assets/index/wifi_1.png" alt class="car" style='width: 22px;height: 16px;'>
      <span style="flex-grow:1"></span>
      <Poptip placement="bottom" width="80" trigger="hover">
        <img src="../assets/index/buju.png" alt class="car">
        <div class="api" slot="content">
          <Button class="api-button" type="text" @click="setLayout(2)">两列布局</Button>
          <Button class="api-button" type="text" @click="setLayout(3)">三列布局</Button>
        </div>
    </Poptip>
      <img src="../assets/index/color.png" alt class="car">
      <img src="../assets/index/help.png" alt class="car">
    </header>
    <div class="contain layout1" :style="`grid-auto-rows: ${wd}px; grid-template-columns: repeat(${laypout}, 1fr);`">
      <Card class="card" />
      <Card class="card" />
      <Card class="card" />
      <Card class="card" />
      <Card class="card" />
      <Card class="card" />
      <Card class="card" />
      <Card class="card" />
      <Card class="card" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { setTimeout } from 'timers'
export default {
  name: 'WebList',
  data () {
    return {
      form: {
        vol: [50],
        son: [50]
      },
      wd: 320,
      laypout: '2'
    }
  },
  methods: {
    resizeHandle () {
      this.wd = document.querySelector('.contain .card').offsetWidth / 16 * 9
    },
    setLayout (n) {
      this.laypout = n
      setTimeout(() => this.resizeHandle(), 0)
    }
  },
  computed: {
    ...mapState(['isCollapsed'])
  },
  watch: {
    isCollapsed (value) {
      setTimeout(() => this.resizeHandle(), 300)
      return value
    }
  },
  mounted () {
    this.resizeHandle()
    window.addEventListener('resize', this.resizeHandle)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.resizeHandle)
  }
}
</script>

